import { useEffect, useState } from 'react';
import { RostateApi, RostateSaveApi, RostateDelApi } from 'utils/Api';
import { Table, Button, Modal, Upload, message } from 'antd';
import { PlusOutlined } from '@ant-design/icons';
const { Column } = Table;

const Rotate = () => {
  const [tableData, setTableData] = useState();
  const [visible, setVisible] = useState(false);
  const [imageUrl, setImageUrl] = useState(null);

  useEffect(() => {
    RostateApi().then((res) => {
      setTableData(
        res.data.map((item) => ({
          ...item,
          _id: item._id.slice(0, 7),
          id: item._id,
        }))
      );
    });
  }, []);

  const del = (record) => {
    RostateDelApi(record.id).then((res) => {
      const newTableData = tableData.filter((item) => item.id !== record.id);
      setTableData(newTableData);
    });
  };

  const onChange = (info) => {
    if (info.file.status === 'done') {
      message.success('文件上传成功');
      setImageUrl(info.file.response);
    } else if (info.file.status === 'error') {
      message.error('文件上传失败');
    }
  };

  const onOk = () => {
    RostateSaveApi({ imageUrl }).then((res) => {
      message.success('添加成功');
      onCancel();
      setTableData([
        ...tableData,
        {
          _id: res.data._id.slice(0, 7),
          id: res.data._id,
          imgUrl: res.data.imgUrl,
        },
      ]);
    });
  };

  const onCancel = () => {
    setVisible(false);
    setImageUrl(null);
  };
  return (
    <>
      <Button
        type="primary"
        className="bg-blue-600"
        onClick={() => setVisible(true)}
      >
        添加横幅
      </Button>
      <Modal
        title="添加首页横幅"
        centered
        visible={visible}
        onOk={onOk}
        onCancel={onCancel}
        width={1000}
        okButtonProps={{ type: 'primary' }}
        cancelButtonProps={{ danger: true, type: 'primary' }}
        okText="确认"
        cancelText="取消"
        closable={false}
        keyboard={false}
        maskClosable={false}
      >
        <Upload
          style={{
            display: 'flex',
            justifyContent: 'center',
            alignItems: 'center',
          }}
          name="file"
          action={process.env.REACT_APP_UPLOAD_SERVER}
          headers={
            localStorage.getItem('__admin_token')
              ? { Authorization: `Bearer ${localStorage.__admin_token}` }
              : null
          }
          showUploadList={false}
          onChange={onChange}
        >
          {imageUrl ? (
            <img
              src={process.env.REACT_APP_SERVER + imageUrl}
              alt="avatar"
              style={{ width: '18rem', height: '6rem' }}
            />
          ) : (
            <div
              style={{
                display: 'flex',
                justifyContent: 'center',
                alignItems: 'center',
                width: '18rem',
                height: '6rem',
              }}
            >
              <PlusOutlined />
              <div style={{ marginTop: 8 }}>Upload</div>
            </div>
          )}
        </Upload>
      </Modal>
      <Table dataSource={tableData} rowKey="id">
        <Column title="编号" dataIndex="_id" />
        <Column
          title="缩略图"
          dataIndex="imgUrl"
          render={(_, record) => (
            <img
              src={process.env.REACT_APP_SERVER + record.imgUrl}
              alt="img"
              style={{ width: '12rem', height: '6rem' }}
            />
          )}
        />
        <Column
          title="操作"
          dataIndex=""
          render={(_, record) => (
            <div className="flex gap-1">
              <Button danger onClick={() => del(record)}>
                删除
              </Button>
            </div>
          )}
        />
      </Table>
    </>
  );
};

export default Rotate;
